// frontend/src/components/FlightInstrumentsPanel.js

import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Sparkles, Map, Gauge, Activity } from 'lucide-react';

import { useAssessment } from '../context/AssessmentContext';
import { useUserProfile } from '../context/UserProfileContext';
import { useFlightStatus } from '../context/FlightStatusContext';
import { getPlaneLevel as getPlaneFromScore, toFlightMiles } from '../utils/flightMetrics';

const StatPill = ({ label, value }) => {
  const safeValue = Number.isFinite(value) ? Math.round(value * 100) : 0;

  return (
    <div className="flex items-center justify-between rounded-lg bg-slate-900/70 border border-slate-700/80 px-3 py-2">
      <span className="text-xs text-slate-300">{label}</span>
      <span className="text-sm font-semibold text-slate-50">{safeValue}</span>
    </div>
  );
};

const ProgressBar = ({ value = 0, max = 100 }) => {
  const ratio = Math.max(0, Math.min(1, max ? value / max : 0));

  return (
    <div className="h-2 w-full rounded-full bg-slate-800 overflow-hidden">
      <div
        className="h-full rounded-full bg-gradient-to-r from-sky-500 to-cyan-400"
        style={{ width: `${ratio * 100}%` }}
      />
    </div>
  );
};

const FlightInstrumentsPanel = () => {
  const navigate = useNavigate();

  // Contexts
  const {
    getFlightMiles,
    getPlaneLevel: getPlaneFromAssessment,
    getReaoScores,
    latestAssessment,
  } = useAssessment();

  const { userProfile } = useUserProfile();
  const { liveStatus } = useFlightStatus();

  // --- Live vs saved state ---------------------------------------------------

  const liveActive =
    liveStatus?.isActive && (liveStatus?.answeredCount ?? 0) > 0;

  const flightMiles = liveActive
    ? toFlightMiles(liveStatus.combinedScore || 0)
    : getFlightMiles();

  const planeLevel = liveActive
    ? getPlaneFromScore(liveStatus.combinedScore || 0)
    : getPlaneFromAssessment();

  const reaoScores = !liveActive && latestAssessment
    ? getReaoScores()
    : null;

  const questionsAnswered = liveActive
    ? liveStatus.answeredCount
    : latestAssessment
      ? Object.keys(latestAssessment.responses || {}).length
      : 0;

  const totalQuestions = liveActive
    ? liveStatus.totalQuestions || 10
    : 10;

  const safeMiles = Number.isFinite(flightMiles) ? flightMiles : 0;

  // --- Actions ---------------------------------------------------------------

  const handleFeelLucky = () => {
    const assessmentId = localStorage.getItem('latestAssessmentId');

    if (assessmentId) {
      navigate(`/flight-deck/${assessmentId}`, {
        state: { triggerLucky: true },
      });
    } else {
      navigate('/flight-deck');
    }
  };

  const handleOpenMap = () => {
    const assessmentId = localStorage.getItem('latestAssessmentId');

    if (assessmentId) {
      navigate(`/flight-deck/${assessmentId}`);
    } else {
      navigate('/flight-deck');
    }
  };

  // --- Render ---------------------------------------------------------------

  return (
    <aside className="w-full lg:w-80 xl:w-96 space-y-4">
      {/* Panel shell */}
      <div className="rounded-2xl bg-slate-950/80 border border-slate-800/80 shadow-lg shadow-slate-900/50 p-4 space-y-4">
        {/* Header */}
        <header className="flex items-center justify-between">
          <div>
            <h2 className="text-sm font-semibold text-slate-100 flex items-center gap-2">
              <Gauge className="h-4 w-4 text-sky-400" />
              Flight Instruments
            </h2>
            <p className="text-[11px] text-slate-400 mt-0.5">
              Your marketing status at a glance
            </p>
          </div>

          {liveActive && (
            <span className="inline-flex items-center gap-1 rounded-full bg-emerald-900/40 border border-emerald-600/60 px-2 py-1 text-[10px] font-medium text-emerald-200">
              <Activity className="h-3 w-3" />
              Live
            </span>
          )}
        </header>

        {/* Current Aircraft */}
        <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-2">
          <div className="flex items-center justify-between">
            <span className="text-xs font-medium text-slate-300">
              Current Aircraft
            </span>
            {liveActive && (
              <span className="text-[10px] rounded-full bg-sky-900/60 px-2 py-0.5 text-sky-200">
                Updating
              </span>
            )}
          </div>

          <div className="flex items-center gap-3">
            <div className="flex h-9 w-9 items-center justify-center rounded-full bg-slate-800/80 text-lg">
              {planeLevel?.emoji || ''}
            </div>
            <div>
              <div className="text-sm font-semibold text-slate-50">
                {planeLevel?.name || 'Not Yet Assessed'}
              </div>
              <div className="text-[11px] text-slate-400">
                {planeLevel?.description ||
                  'Complete an assessment to see your aircraft level.'}
              </div>
            </div>
          </div>
        </section>

        {/* Flight miles + progress */}
        <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-2">
          <div className="flex items-center justify-between">
            <span className="text-xs font-medium text-slate-300">
              Flight Miles
            </span>
            {liveActive && (
              <span className="text-[10px] rounded-full bg-slate-800 px-2 py-0.5 text-slate-300">
                Preview
              </span>
            )}
          </div>

          <div className="flex items-baseline justify-between">
            <span className="text-lg font-semibold text-slate-50">
              {safeMiles}
            </span>
            <span className="text-[11px] text-slate-400">
              / 1000 miles
            </span>
          </div>

          {liveActive && (
            <p className="text-[11px] text-slate-400">
              Score: {Math.round((liveStatus.currentScore || 0))}/100
            </p>
          )}

          <ProgressBar value={safeMiles} max={1000} />
        </section>

        {/* Assessment progress  live mode emphasised, otherwise compact */}
        {(liveActive || questionsAnswered > 0) && (
          <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-2">
            <div className="flex items-center justify-between">
              <span className="text-xs font-medium text-slate-300">
                Assessment Progress
              </span>
            </div>
            <div className="flex items-baseline justify-between">
              <span className="text-sm font-semibold text-slate-50">
                {questionsAnswered} / {totalQuestions}
              </span>
              {liveActive && (
                <span className="text-[11px] text-slate-400">
                  Your score updates with each answer.
                </span>
              )}
            </div>
            <ProgressBar
              value={questionsAnswered}
              max={totalQuestions || 10}
            />
          </section>
        )}

        {/* REAO scores  only when we have a completed assessment */}
        {!liveActive && latestAssessment && reaoScores && (
          <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-xs font-medium text-slate-300">
                REAO Dimensions
              </span>
            </div>

            <div className="grid grid-cols-2 gap-2">
              <StatPill label="Readiness" value={reaoScores.readiness} />
              <StatPill label="Efficiency" value={reaoScores.efficiency} />
              <StatPill label="Alignment" value={reaoScores.alignment} />
              <StatPill label="Opportunity" value={reaoScores.opportunity} />
            </div>
          </section>
        )}

        {/* Flight profile */}
        {(userProfile.role ||
          userProfile.industry ||
          userProfile.companySize ||
          userProfile.teamSize) && (
          <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-1.5">
            <div className="flex items-center justify-between mb-1">
              <span className="text-xs font-medium text-slate-300">
                Flight Profile
              </span>
            </div>

            {userProfile.role && (
              <div className="flex justify-between text-[11px] text-slate-300">
                <span className="text-slate-400">Role</span>
                <span className="font-medium text-slate-100">
                  {userProfile.role}
                </span>
              </div>
            )}

            {userProfile.industry && (
              <div className="flex justify-between text-[11px] text-slate-300">
                <span className="text-slate-400">Industry</span>
                <span className="font-medium text-slate-100">
                  {userProfile.industry}
                </span>
              </div>
            )}

            {userProfile.companySize && (
              <div className="flex justify-between text-[11px] text-slate-300">
                <span className="text-slate-400">Company</span>
                <span className="font-medium text-slate-100">
                  {userProfile.companySize}
                </span>
              </div>
            )}

            {userProfile.teamSize && (
              <div className="flex justify-between text-[11px] text-slate-300">
                <span className="text-slate-400">Team</span>
                <span className="font-medium text-slate-100">
                  {userProfile.teamSize}
                </span>
              </div>
            )}
          </section>
        )}

        {/* Quick actions */}
        <section className="rounded-xl bg-slate-900/80 border border-slate-700/70 p-3 space-y-2">
          <div className="flex items-center justify-between">
            <span className="text-xs font-medium text-slate-300">
              Quick Actions
            </span>
          </div>

          <div className="flex flex-col gap-2">
            <button
              type="button"
              onClick={handleFeelLucky}
              className="inline-flex items-center justify-center gap-2 rounded-lg bg-gradient-to-r from-fuchsia-500 to-pink-500 px-3 py-2 text-xs font-semibold text-white shadow-md shadow-fuchsia-900/40 hover:brightness-105 transition"
            >
              <Sparkles className="h-4 w-4" />
              I Feel Lucky  Show Journeys
            </button>

            <button
              type="button"
              onClick={handleOpenMap}
              className="inline-flex items-center justify-center gap-2 rounded-lg border border-sky-500/60 bg-sky-900/40 px-3 py-2 text-xs font-semibold text-sky-50 hover:bg-sky-900/70 transition"
            >
              <Map className="h-4 w-4" />
              Open Journey Map
            </button>

            {liveActive && (
              <p className="text-[11px] text-slate-400">
                Complete the assessment to unlock full journey recommendations.
              </p>
            )}
          </div>
        </section>
      </div>
    </aside>
  );
};

export default FlightInstrumentsPanel;
