// frontend/src/config/api.js

const isLocalhost =
  typeof window !== "undefined" &&
  (window.location.hostname === "localhost" ||
    window.location.hostname === "127.0.0.1");

// Local FastAPI server on your laptop
const LOCAL_API_BASE_URL = "http://localhost:8000/api";

// Render backend for the live site
const PROD_API_BASE_URL = "https://flightv4-3.onrender.com/api";

// Optional override (not used on Hostinger, but kept for flexibility)
const envUrl = process.env.REACT_APP_BACKEND_URL;

export const API_BASE_URL =
  envUrl || (isLocalhost ? LOCAL_API_BASE_URL : PROD_API_BASE_URL);
